.ProjectCardWrapper {
    width: 100%;
    height: 100vh;
    margin-bottom: 10vh;
    display: flex;
    .backers {
        width: 15%;
        margin-top: 5vh;
        background-color: #f1f1f1;
        height: 66vh; }
    .ProjectCard {
        width: 80%;
        display: flex;
        flex-direction: column;
        align-items: center;
        .slider {
            margin-top: 15vh;
            width: 70%;
            height: 100%;
            background-color: white;
            display: flex;
            justify-content: space-between;
            border: 4px solid black;
            box-sizing: border-box;
            background-size: cover !important;
            span {
                -moz-user-select: none;
                -khtml-user-select: none;
                -webkit-user-select: none;
                user-select: none;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 32px;
                font-weight: 900;
                width: 50%;
                cursor: pointer;
                transition: 0.3s;
                opacity: 0;
                color: white; }
            span:hover {
                opacity: 1;
                background-color: rgba(0, 0, 0, 0.4); } }
        .links {
            width: 140%;
            display: flex;
            justify-content: space-between;
            cursor: pointer;
            a {
                text-decoration: none;
                color: black;
                width: 50%;
                height: 15vh;
                background-color: white;
                transition: 0.3s;
                cursor: pointer;
                font-size: 32px;
                font-weight: 900;
                display: flex;
                justify-content: center;
                align-items: center; }
            a:hover {
                background-color: rgba(0, 0, 0, 0.4); } } } }

